import {
  Grid,
  GridItem,
  Stack,
  Box,
  Heading,
  FormControl,
  Input,
  FormLabel,
  Text,
  Textarea,
  Image,
  Flex,
  Button,
} from "@chakra-ui/react";
import { DevTool } from "@hookform/devtools";
import { useForm } from "react-hook-form";
import img from "../../icons/inputImage.png";

interface Support {
  CityName: string;
}

const Support: React.FC = () => {
  const {
    register,
    handleSubmit,
    reset,
    control,
    formState: { errors },
  } = useForm<Support>();

  return (
    <Stack h="100%" mt={1}>
      <Grid templateColumns="repeat(2, 1fr)" gap={6} p={4}>
        <GridItem w="100%" shadow={"lg"}>
          <Box>
            <Heading as="h3" size="lg" textAlign={"center"} pb={2}>
              Raise Issue
            </Heading>
            <Box px={8} py={4}>
              <Box mb={4}>
                <Text mb="8px">Page Name :</Text>
                <Input
                  placeholder="Enter Page Name"
                  size="base"
                  p={1}
                  rounded={"md"}
                />
              </Box>
              <Box>
                <Text mb="8px">Issue :</Text>

                <Textarea placeholder="Enter Your Issue" />
              </Box>
            </Box>
            <Box px={8} py={4}>
              <FormLabel htmlFor="image">Screenshots :</FormLabel>
              <Flex
                maxW="md"
                borderWidth={1}
                borderRadius="md"
                boxShadow="md"
                justifyContent={"space-evenly"}
                // m={"auto"}
                alignItems={"center"}
              >
                <Box position="relative">
                  <Image
                    src={img}
                    alt="Image Preview"
                    w="100%"
                    // h="8rem"
                    borderRadius="md"
                    borderWidth={1}
                    borderColor="gray.200"
                  />
                  <Input
                    type="file"
                    id="image"
                    name="image"
                    position="absolute"
                    top={0}
                    left={0}
                    opacity={0}
                    w="100%"
                    h="100%"
                    cursor="pointer"
                    zIndex={2}
                  />
                </Box>
                <Box position="relative">
                  <Image
                    src={img}
                    alt="Image Preview"
                    w="100%"
                    // h="8rem"
                    borderRadius="md"
                    borderWidth={1}
                    borderColor="gray.200"
                  />
                  <Input
                    type="file"
                    id="image"
                    name="image"
                    position="absolute"
                    top={0}
                    left={0}
                    opacity={0}
                    w="100%"
                    h="100%"
                    cursor="pointer"
                    zIndex={2}
                  />
                </Box>
              </Flex>
              <Box
                mt={"6"}
                display={"flex"}
                justifyContent={"flex-end"}
                alignItems={"center"}
              >
                <Button
                  loadingText="Please wait..."
                  type="submit"
                  size={"sm"}
                  _hover={{ bg: "#FF9000" }}
                  w="8.188rem"
                  bg="#FF9000"
                  borderRadius={"0.25rem"}
                  color="whiteAlpha.900"
                  fontWeight={"700"}
                  fontSize={"1rem"}
                >
                  Submit
                </Button>
              </Box>
            </Box>
          </Box>
        </GridItem>
        <GridItem w="100%" shadow={"lg"}>
          <Heading as="h5" size="md" textAlign={"center"} pb={2}>
            Your histories will show here
          </Heading>

          <Box px={2} bg="blue.100">
            <Flex
              justifyContent={"space-between"}
              alignItems={"start"}
              gap={4}
              p={2}
            >
              <Box>
                <Text fontWeight={"bold"}>Page Name</Text>
                <Text fontWeight={"light"} fontSize={"sm"}>
                  Lorem, ipsum dolor sit amet consectetur
                </Text>
              </Box>
              <Box>
                <Text>Timing</Text>
              </Box>
            </Flex>
          </Box>
          <Box px={2} bg="gray.100">
            <Flex
              justifyContent={"space-between"}
              alignItems={"start"}
              gap={4}
              p={2}
            >
              <Box>
                <Text fontWeight={"bold"}>Page Name</Text>
                <Text fontWeight={"light"} fontSize={"sm"}>
                  Lorem, ipsum dolor sit amet consectetur adipisicing
                </Text>
              </Box>
              <Box>
                <Text>Timing</Text>
              </Box>
            </Flex>
          </Box>
          <Box px={2} bg="blue.100">
            <Flex
              justifyContent={"space-between"}
              alignItems={"start"}
              gap={4}
              p={2}
            >
              <Box>
                <Text fontWeight={"bold"}>Page Name</Text>
                <Text fontWeight={"light"} fontSize={"sm"}>
                  Lorem, ipsum dolor sit amet consectetur hhh
                </Text>
              </Box>
              <Box>
                <Text>Timing</Text>
              </Box>
            </Flex>
          </Box>
        </GridItem>
      </Grid>
      <DevTool control={control} />
    </Stack>
  );
};

export default Support;
